$font-main: "TT-Norms-Pro", sans-serif;

$color-bg: #ffffff;
$color-bg1: #539bf5;
$color-hover: #539bf5;
$color-text: #000000;

$primary: #4F2EDC;
$primary-light: #947ADA;
$gray: #9A9999;


%font-small {
  font-size: 1.8rem;
  line-height: 2.5rem;
  font-weight: 700;
}

%font-default {
  font-size: 2rem;
  line-height: 2.6rem;
  font-weight: 400;
}

%font-btn {
  font-size: 2.5rem;
  line-height: 3.1rem;
  font-weight: bold;
}

%font-title {
  font-size: 5rem;
  line-height: 7rem;
  font-weight: bold;
}

%btn-hover {
  border-color: black;
  background: #000000;
  color: #ffffff;
}
